.checkbox-label {
  display: block;
  cursor: pointer;
  position: relative;
  padding-left: 32px;
  top: 4px;
  font-size: 14px; }

input[type="checkbox"],
.checkbox-label__icon,
.checkbox-label__icon:before {
  position: absolute;
  left: 0;
  top: 0;
  width: 16px;
  height: 16px; }

input[type="checkbox"] {
  opacity: 0; }

.checkbox-label__icon::before,
.checkbox-label__icon::after {
  position: absolute;
  content: "";
  display: inline-block; }

.checkbox-label__icon:before {
  border: 1px solid #fff; }

.checkbox-label__icon::after {
  height: 5px;
  width: 8px;
  border-left: 2px solid;
  border-bottom: 2px solid;
  transform: rotate(-45deg);
  left: 4px;
  top: 4px; }

input[type="checkbox"] + .checkbox-label__icon::after {
  content: none; }

input[type="checkbox"]:checked + .checkbox-label__icon::after {
  content: ""; }

input[type="checkbox"]:focus + .checkbox-label__icon::before {
  outline: #000 auto 3px; }

